import { Component, OnInit, ElementRef, Renderer2, HostListener, OnDestroy } from '@angular/core';
import * as P from 'pixi.js';
import { Subject, BehaviorSubject, Observable, forkJoin, fromEvent, merge } from 'rxjs';
import { filter, mapTo, take, takeUntil, startWith, debounceTime } from 'rxjs/operators';
import { isTrue, use } from 'src/utils/rxjs';
import { remToPx } from 'src/utils/dom';
import { limitNumber } from 'src/utils/number';

const T_CHRISTMASTREE = 'assets/sprites/christmastree.png';
const T_ROOM = 'assets/sprites/room.jpg';

interface Size {
  width: number;
  height: number;
}

class Sprite extends P.Sprite {
  vx = 0;
  vy = 0;
}

@Component({
  selector: 'vk-nyscene-pixi',
  templateUrl: './pixi.component.html',
  styleUrls: [
    './pixi.component.css'
  ]
})
export class PixiComponent implements OnInit, OnDestroy {
  public loaderProgress$: Subject<number> = new Subject;

  private _destroy$: Subject<void> = new Subject;

  private _domReady$ = new BehaviorSubject(false);

  private _keydown$ = fromEvent<KeyboardEvent>(document, 'keydown');

  private _keyup$ = fromEvent<KeyboardEvent>(document, 'keyup');

  private _keyEvent$ = use(() => merge(this._keydown$, this._keyup$));

  private _pixiApp: P.Application = new P.Application({
    antialias: true,
    transparent: true
  });

  private _resize$: Subject<Size> = new Subject;

  private _texturesReady$ = new BehaviorSubject(false);

  constructor(
    private _hostRef: ElementRef,
    private _renderer: Renderer2
  ) {
    this._loadTextures([
      T_CHRISTMASTREE,
      T_ROOM
    ]);

    forkJoin(
      this._domReady$.pipe(isTrue(), take(1)),
      this._texturesReady$.pipe(isTrue(), take(1))
    ).pipe(
      take(1),
      takeUntil(this._destroy$)
    ).subscribe(() => this._setup());
  }

  ngOnDestroy() {
    this._destroy$.next();
    this._destroy$.complete();
  }

  ngOnInit() {
    this._renderer.appendChild(this._hostRef.nativeElement, this._pixiApp.view);
    this._onResize();
    this._domReady$.next(true);

    this._texturesReady$.pipe(
      filter(ready => ready === true),
      take(1)
    ).subscribe(() => this._setup());
  }

  private _getContainerSize(): Size {
    const el: HTMLElement = this._hostRef.nativeElement;
    return {
      width: el.offsetWidth,
      height: el.offsetHeight
    };
  }

  private _getTexture(key: string): P.Texture {
    return P.loader.resources[key].texture;
  }

  private _handleKeyEvent(e: KeyboardEvent, sprite: Sprite): void {
    const pressed: boolean = e.type === 'keydown';
    const accelerate: boolean = pressed && e.repeat;

    e.preventDefault();
    e.stopPropagation();

    switch (e.key) {
      case 'ArrowDown':
        sprite.vy = pressed ? sprite.vy || 1 : 0;
        if (accelerate) {
          sprite.vy += 1;
        }
        break;
      case 'ArrowUp':
        sprite.vy = pressed ? sprite.vy || -1 : 0;
        if (accelerate) {
          sprite.vy -= 1;
        }
        break;
      case 'ArrowLeft':
        sprite.vx = pressed ? sprite.vx || -1 : 0;
        if (accelerate) {
          sprite.vx -= 1;
        }
        break;
      case 'ArrowRight':
        sprite.vx = (pressed ? sprite.vx || 1 : 0);
        if (accelerate) {
          sprite.vx += 1;
        }
        break;
    }
  }

  private _loadTextures(...loaderParams: any[]): Observable<void> {
    this._texturesReady$.next(false);
    P.loader
      .add(...loaderParams)
      .on('progress', (loader, resource) => this.loaderProgress$.next(loader.progress))
      .load(() => this._texturesReady$.next(true));
      return this._texturesReady$.pipe(
        filter(ready => ready === true),
        mapTo(undefined),
        take(1)
      );
  }

  @HostListener('window:resize', ['$event'])
  private _onResize(): void {
    const size = this._getContainerSize();
    this._pixiApp.renderer.resize(size.width, size.height);
    this._resize$.next(size);
  }

  private _setup() {
    const containerSize = this._getContainerSize();
    const spriteChristmasTree = new Sprite(this._getTexture(T_CHRISTMASTREE));

    spriteChristmasTree.anchor.x = 0.5;
    spriteChristmasTree.anchor.y = 0.5;
    spriteChristmasTree.position = new P.Point(containerSize.width / 2, containerSize.height / 1.5);

    const spriteRoom = new Sprite(this._getTexture(T_ROOM));
    spriteRoom.anchor.x = 0.5;
    spriteRoom.position = new P.Point(containerSize.width / 2, 0);

    this._resize$.pipe(
      startWith(this._getContainerSize()),
      takeUntil(this._destroy$)
    ).subscribe((size: Size) => {
      const treeProportions = spriteChristmasTree.height / spriteChristmasTree.width;
      const roomProportions = spriteRoom.height / spriteRoom.width;

      const treeHeight = spriteChristmasTree.height = size.height / 2;
      spriteChristmasTree.width = Math.round(treeHeight / treeProportions);

      const roomHeight = spriteRoom.height = size.height;
      spriteRoom.width = Math.round(roomHeight / roomProportions);

      spriteRoom.position.set(size.width / 2, 0);
    });

    this._keyEvent$.pipe(
      takeUntil(this._destroy$)
    ).subscribe(e => this._handleKeyEvent(e, spriteChristmasTree));

    this._pixiApp.stage.addChild(spriteRoom);
    this._pixiApp.stage.addChild(spriteChristmasTree);

    this._pixiApp.ticker.add(this._applyVelocity.bind(this, [
      spriteChristmasTree
    ]));
  }

  private _applyVelocity(objects: Sprite[], delta: number) {
    objects.forEach(o => {
      o.x = limitNumber(o.x + o.vx, [0, this._pixiApp.view.width]);
      o.y = limitNumber(o.y + o.vy, [0, this._pixiApp.view.height]);
    });
  }
}
