import { OperatorFunction, Observable, of } from 'rxjs';
import { filter, switchMap } from 'rxjs/operators';

export function isTrue(): OperatorFunction<boolean, boolean> {
  return filter(x => x === true);
}

export function use<T>(fn: () => Observable<T>) {
  return of(null).pipe(
    switchMap(fn)
  );
}
